body {
  font-family: "Baloo Da 2", cursive;
}

/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.navlist {
  list-style: none;
}

.navlinks {
  display: inline-block;
  margin: 0 20px;
}

.navlinks a {
  color: white;
  text-decoration: none;
}

.navlinks a.active {
  color: green;
}

.stats-wrapper h2,
.stats-wrapper h3 {
  margin: 5px 0;
}

.stats-wrapper div:first-child {
  color: blue;
}

.stats-wrapper div:nth-child(2) {
  color: green;
}

.stats-wrapper div:nth-child(3) {
  color: red;
}

.stats-wrapper div:nth-child(5) {
  color: #e600b5;
}

.stats-wrapper p {
  font-size: 18px;
}

.stats-wrapper {
  /* display: flex;
  justify-content: space-evenly; */
  /* align-items: center; */
  text-align: center;
  background-color: azure;
  margin-bottom: 20px;
  padding: 50px 0;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
  -moz-box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
  box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
}

thead tr {
  background-color: black;
  color: white;
  font-size: 18px;
}

tbody tr:nth-child(odd) {
  background-color: rgb(214, 214, 214);
}

td:first-child {
  text-align: left;
}

.world-stats div:first-child {
  color: green;
}

.world-stats div:nth-child(2) {
  color: red;
}

.world-stats div:nth-child(3) {
  color: black;
}

.search-results {
  /* display: flex;
  align-content: center; */
}

.country-stats {
  background: linear-gradient(45deg, #d6f9ff 0%, rgb(214, 214, 214) 100%);

  margin-top: 20px;
  -webkit-box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
  -moz-box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
  box-shadow: 10px 10px 5px 0px rgba(219, 219, 219, 1);
}

.country-stats h3:nth-child(2) {
  color: green;
}

.country-stats h3:nth-child(3) {
  color: blue;
}

.country-stats h3:nth-child(4) {
  color: red;
}

.searchbar form {
  /* display: flex;
justify-content: center; */
}

.searchbar input {
  width: 75% !important;
  /* height: 30px; */
  font-size: 18px;
  border-radius: 0;
  /* padding: 0 0 0 5px; */
  outline: none;
}

.searchbar button {
  /* height: 34px; */
  border-radius: 0;
  padding-top: 7.5px;
  padding-bottom: 7.5px;
}

.searchbar .form-control:focus {
  box-shadow: none;
  border: 1px solid #ced4da;
}

.country-center {
  text-align: center;
  flex-grow: 1;
}

.auto-suggest-box .country-list {
  background: linear-gradient(45deg, #d6f9ff 0%, rgb(214, 214, 214) 100%);
  width: 75%;
  margin: 0;
  padding: 0 0 0 5px;
  list-style: none;
  text-align: left;
  margin-right: 4.9%;
  font-size: 18px;
  height: 190px;
  overflow-y: scroll;
  color: green;
  font-weight: bold;
  z-index: 1;
}

.country-list li {
  cursor: pointer;
}

.autosuggest-wrapper {
  position: relative;
}

.auto-suggest-box {
  display: flex;
  justify-content: center;
  position: absolute;
  right: 0;
  left: 4px;
}

.auto-suggest-box .displayOn {
  display: block !important;
}

.auto-suggest-box .displayOff {
  display: none !important;
}

.top-countries-row {
  padding: 10px;
}

.top-countries:nth-child(odd) {
  background: rgba(0, 0, 0, .05);
  padding-top: 8px;
  padding-bottom: 8px;
}

.top-countries:first-child {
  background: #1d1b1b;
  color: white;
  padding-top: 10px;
  padding-bottom: 10px;
}

.top-countries :nth-child(n) {
  flex-basis: calc(100%/5);
}



@media screen and (max-width: 500px) {
  .searchbar {
    position: relative;
  }

  .auto-suggest-box {
    left: 5%;
    justify-content: left;

  }
}